import * as React from 'react';
import Backdrop from '@mui/material/Backdrop';
import Box from '@mui/material/Box';
import Modal from '@mui/material/Modal';
import Fade from '@mui/material/Fade';
import Button from '../Button'
import Stepper from '@mui/material/Stepper';
import Step from '@mui/material/Step';
import StepLabel from '@mui/material/StepLabel';
import Grid from '@mui/system/Unstable_Grid';
import { width } from '@mui/system';
import Body from './Body';
import { Typography } from '@mui/material';
import colors from '../../constants/colors';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: '28%',
  minWidth: '300px',
  minHeight: '200px',
  height: "220px",
  bgcolor: 'background.paper',
  border: '1px solid #DDD',
  boxShadow: 10,
  borderRadius: "11px",
  p: 4,
  position:'relative',
  paddingTop:"16px 10px"
};

export default function TransitionsModal({title, description, type, showModal, onClose}) {
  const [open, setOpen] = React.useState(false);
  const handleOpen = () => setOpen(showModal);
  const handleClose = () => setOpen(false);
  const handleClick = () => {
    setOpen(!showModal);
  };

  // type="modifie"
  
  if(type==='alert'){
    return (
      <div>
        {/* <Button onClick={handleOpen}>Ouvrir la modale</Button> */}
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          open={showModal}
          onClose={onClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={showModal}> 
            <Box sx={style}>
              <Typography sx={{ fontWeight:"bold", fontSize: "16px", color:"black", textAlign:"center"}}>{title?title:"Relié à"}</Typography>
              <Typography sx={{ fontSize: "14px", color:"black", mb:2, textAlign:"center", marginTop:"10px"}}>{description?description:"Voyus êtes sur le point de donner des points à {userName}"}</Typography>
      
              <div style={{ display:"flex", flexDirection:"row", marginTop:"10px",
                justifyContent:'center', left: "22%", marginTop:'20px', position:"absolute", bottom:"20px"}}>
            
                  <Button text="Annuler" btnStyle={{ color: colors.cost.blue, backgroundColor:"white", width:"100px",heigth:2, marginRight:4}} onClick={onClose}></Button>
                  <Button text="Enregistrer" btnStyle={{ color: "white", backgroundColor: colors.cost.blue, width:"100px", heigth:2}}
                  ></Button>
              </div>
  
            </Box>
          </Fade>
        </Modal>
      </div>
    );
  } else{
    return (
      <div>
        {/* <Button onClick={handleOpen}>Ouvrir la modale</Button> */}
        <Modal
          aria-labelledby="transition-modal-title"
          aria-describedby="transition-modal-description"
          open={showModal}
          onClose={onClose}
          closeAfterTransition
          BackdropComponent={Backdrop}
          BackdropProps={{
            timeout: 500,
          }}
        >
          <Fade in={showModal}>
            <Box sx={style}>
            <Typography sx={{ fontWeight:"bold", fontSize: "16px", color:"black", mb:2, textAlign:"center"}}>{title?title:"Relié à"}</Typography>
            <Typography sx={{ fontSize: "12px", color:"black", mb:2, textAlign:"center"}}>{description?description:"Voyus êtes sur le point de donner des points à {userName}"}</Typography>
  
            <Body type={type} onClose={false}/>
  
            <div style={{ display:"flex", flexDirection:"row", marginTop:"10px",
               justifyContent:'center', left: "20%", marginTop:'20px'}}>
          
                <Button text="Annuler" btnStyle={{ color: colors.cost.blue, backgroundColor:"white", width:"100px",heigth:2, marginRight:4}} onClick={handleClick}></Button>
                <Button text="Enregistrer" btnStyle={{ color: "white", backgroundColor: colors.cost.blue, width:"100px", heigth:2}}
                ></Button>
            </div>
  
            </Box>
          </Fade>
        </Modal>
      </div>
    );
  }
  
}