export default function index() {
    return (
        <Box>
            <PageHeader title={'Celebrites'} subtitle={'240 celebrites'} secondHandleText="Creer une celebrite" />
            <TableContainer component={Paper} sx={{marginTop: 5}}>
                <Table sx={{ minWidth: 650 }} aria-label="caption table">
                    <TableHead style={{backgroundColor: colors.light.gray200}}>
                        <TableRow>
                            <TableCell>Nom<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Categorie</TableCell>
                            <TableCell align="left">Clubs asocie<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Membre<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Presents??</TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        {rows.map((row) => (
                            <TableRow key={row.pseudo}>
                                <TableCell align="left" component="th" scope="row">
                                    <Item title={row.pseudo} titleStyle={{fontWeight: '400', fontSize: 14}}/>
                                </TableCell>
                                <TableCell align="left">{row.email}</TableCell>
                                <TableCell align="left">{row.clubs}</TableCell>
                                <TableCell align="left">{row.num}</TableCell>
                                <TableCell align="left">{row.signal}</TableCell>
                            </TableRow>
                        ))}
                    </TableBody>
                </Table>
            </TableContainer>
        </Box>
    )
}
