import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import React, { useEffect, useState } from 'react';
import CustomButton from '../../../../components/Button';
import Image from '../../../../components/Card/Image';
import ItemsCarousel from 'react-items-carousel';
import FastForwardRounded from '@mui/icons-material/FastForwardRounded';
import FastRewindRounded from '@mui/icons-material/FastRewindRounded';


const commingEvents = [
    '1', '2', '1'
]

const passedEvents = [
    '1', '2', '3', '1', '2'
]
const Events = ({ type, windowHeight, windowWidth }) => {
    const [activeItemIndexPassed, setActiveItemIndexPassed] = useState(0);
    const [activeItemIndexComing, setActiveItemIndexComing] = useState(0);
    const chevronWidth = 0;
    const [itemNumber, setItemNumber] = useState('');

    const handleNumberItem = () => {
        if (windowWidth > 320 && windowWidth < 480) {
            setItemNumber(1)
        } else if (windowWidth > 481 && windowWidth < 768) {
            setItemNumber(2)
        } else if (windowWidth > 769 && windowWidth < 1024) {
            setItemNumber(4)
        } else if (windowWidth > 1025 && windowWidth < 1200) {
            setItemNumber(6)
        } else if (windowWidth > 1201) {
            setItemNumber(8)
        }
    }

    useEffect(() => {
        handleNumberItem();
    }, [windowWidth]);

    return (
        <Box>
            <Box display={ 'flex' } justifyContent='end' marginBottom={ '1' }>
                <CustomButton text={ 'Creer un evenement' } variant='contained' btnStyle={ { color: 'white' } } />
            </Box>
            <Box sx={ {
                width: '100%',
                maxHeight: `${windowHeight - 210}px`, position: 'relative',
                overflowY: 'scroll',
                '&::-webkit-scrollbar': {
                    display: 'none',
                },
            } }>

                <Box sx={ { marginBottom: 2, width: '100%', } }>
                    <Box justifyContent='space-between' sx={ { marginRight: "10px", display: "flex", marginBottom: 2 } }>
                        <Typography sx={ { fontWeight: "bold", fontSize: 16 } }>Evènements à venir</Typography>
                        {/* <CustomButton text={ 'Creer un evenement' } variant='contained' btnStyle={ { color: 'white' } } /> */ }
                    </Box>
                    <Box>
                        <div style={ { padding: `0 ${chevronWidth}px` } }>
                            <ItemsCarousel

                                requestToChangeActive={ setActiveItemIndexComing }
                                activeItemIndex={ activeItemIndexComing }
                                numberOfCards={ itemNumber }
                                gutter={ 147.00002   }
                                leftChevron={ <FastRewindRounded /> }
                                rightChevron={ <FastForwardRounded /> }
                                outsideChevron
                                chevronWidth={ chevronWidth }
                            >
                                { commingEvents.map((item) => (
                                    <Box key={ item.id } sx={{ minWidth:"200px", display:"flex", flexDirection:"row" }}>
                                        <Image type={ 'evenement' }  style={{ gap:"20px", }} imgAmis={ 'https://i.pravatar.cc/150?img=5' } />
                                    </Box>
                                )) }
                            </ItemsCarousel>
                        </div>
                    </Box>
                </Box>
                <Box sx={ { marginBottom: 2, width: '100%', } }>
                    <Box sx={ { marginRight: "10px", display: "flex", marginBottom: 2 } }>
                        <Typography sx={ { fontWeight: "bold", fontSize: 16 } }>Evènement passés</Typography>
                    </Box>
                    <Box>
                        <div style={ { padding: `0 ${chevronWidth}px` } }>
                            <ItemsCarousel

                                requestToChangeActive={ setActiveItemIndexPassed }
                                activeItemIndex={ activeItemIndexPassed }
                                numberOfCards={ itemNumber }
                                gutter={ 147.00002 }
                                leftChevron={ <FastRewindRounded /> }
                                rightChevron={ <FastForwardRounded /> }
                                outsideChevron
                                chevronWidth={ chevronWidth }
                            >
                                { passedEvents.map((item) => (
                                    <Box key={ item.id }>
                                        <Image type={ 'evenement' } imgAmis={ 'https://i.pravatar.cc/150?img=5' } />
                                    </Box>
                                )) }
                            </ItemsCarousel>
                        </div>
                    </Box>
                </Box>
            </Box>
        </Box>
    );
};

export default Events
    ;