import { Typography } from '@mui/material';
import { Box } from '@mui/system';
import React from 'react';

const index = ({ children, index, value }) => {
    return (
        <Box
            role="tabpanel"
            hidden={ value !== index }
            id={ `simple-tabpanel-${index}` }
            aria-labelledby={ `simple-tab-${index}` }
        >
            { value === index && (
                <Box sx={ { paddingTop: 3 } }>
                    { children }
                </Box>
            ) }
        </Box>
    );
};

export default index;
