import React from 'react'
import Box from '@mui/material/Box';
import Card from '@mui/material/Card';
import CardContent from '@mui/material/CardContent';
import Typography from '@mui/material/Typography';
// import './Image.css'
import CardMedia from '@mui/material/CardMedia';
import { Button, CardActionArea, CardActions } from '@mui/material';
import cardImage from "../../Images/media.jpg"
import LinkIcon from '@mui/icons-material/Link';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import colors from '../../constants/colors'


export default function Image({ height, width, type, imgSrc,
  inlive, evenement, publicationInfos, imgAmi, imgAmis, pseudo, style }) {
  // type = 'album'
  // type = "concert"
  // type = 'evenement'
  // type = 'amis'
  // type='publication'
  // inlive=true;
  const inliveLogo = [];

  inliveLogo.push(<div className='stickerNoteImage'
    style={ {
      height: 25,
      minWidth: '50px',
      backgroundColor: colors.cost.blue,
      color: "white",
      borderTopRightRadius: 7,
      borderBottomLeftRadius: 7,
      position: "absolute",
      top: 0,
      right: 0,
      marginLeft: 5,
      paddingLeft: 5,
      paddingRight: 8,
      fontSize: 12,
      display: "flex",
      flexDirection: "row",

    } }

  ></div>)

  return (
    <Card sx={ {  width: "auto", boxShadow: 'none', backgroundColor: "#f0f8ff00", position: "relative", borderRadius: "11px" } }>
      { type === 'album' ?
        <CardActionArea sx={ { minWidth: '195px', width: '110%', maxWidth: "200px" } }>
          { inlive === true ? inliveLogo :
            <div className='stickerNoteImage'
              style={ {
                height: 25,
                width: "auto",
                backgroundColor: "#98b8ca8c",
                color: "white",
                borderRadius: 7,
                position: "absolute",
                top: 5,
                marginLeft: 5,
                paddingLeft: 5,
                paddingRight: 8,
                fontSize: 12,
                display: "flex",
                flexDirection: "row",
              } }
            >
              <LinkIcon />
              <p style={ { lineHeight: "0px", marginBottom: "0px", lineHeight: "26px" } }>Outfit de Matt</p>
            </div>
          }


          <CardMedia
            component="img"
            height="200px"
            image={ cardImage }
            alt="Mat pokora"
            sx={ { borderRadius: '11px', ...height, ...width } }

          />
          <MoreHorizIcon sx={ { position: "absolute", color: "#0058FF", top: "170px", right: "10px" } } />

          <CardContent sx={ { height: 100, pr: 0, pl: 0 } }>
            <Typography sx={ { fontSize: 16, fontWeight: '600', color: "#6b6b6b" } } gutterBottom variant="h5" component="p">
              Concerts
            </Typography>
            <Typography sx={ { fontSize: 14 } } variant="body2" color="#6b6b6b">
              32 Médias
            </Typography>
            <Typography mt={ 1 } sx={ { fontSize: 12 } } variant="body2" color="#6b6b6b">
              Créé le 21/02/22 par @elena
            </Typography>
          </CardContent>

        </CardActionArea>


        : type === 'concert' ?
          <CardActionArea sx={ { height: "200px", minWidth: "150px", width: "18%", maxWidth: "200px" } }>

            <CardMedia
              component="img"
              height="200px"
              image={ cardImage }
              alt="Mat pokora"
              sx={ { borderRadius: '11px', ...height, ...width } }
            />
            { inlive ? inliveLogo :
              <MoreHorizIcon sx={ { position: "absolute", color: "white", top: "10px", right: "10px" } } />
            }


          </CardActionArea>


          : type === 'concert' ?
            <CardActionArea >
              { inlive === true ? inliveLogo : <div className='stickerNoteImage'
                style={ {
                  height: 25,
                  width: "auto",
                  backgroundColor: "#98b8ca8c",
                  color: "white",
                  borderRadius: 7,
                  position: "absolute",
                  top: 5,
                  marginLeft: 5,
                  paddingLeft: 5,
                  paddingRight: 8,
                  fontSize: 12,
                  display: "flex",
                  flexDirection: "row",

                } }
              >
                <LinkIcon />
                <p style={ { lineHeight: "0px", marginBottom: "0px", lineHeight: "26px" } }>Outfit de Matt</p>
              </div>
              }

              <CardMedia
                component="img"
                height="200px"
                image={ cardImage }
                alt="Mat pokora"
                sx={ { borderRadius: '11px', ...height, ...width } }
              />
              <MoreHorizIcon sx={ { position: "absolute", color: "#0058FF", top: "170px", right: "10px" } } />

              <CardContent sx={ { height: 100, pr: 0, pl: 0 } }>
                <Typography sx={ { fontSize: 16, fontWeight: '600', color: "#6b6b6b" } } gutterBottom variant="h5" component="p">
                  Concerts
                </Typography>
                <Typography sx={ { fontSize: 14 } } variant="body2" color="#6b6b6b">
                  32 Médias
                </Typography>
                <Typography mt={ 1 } sx={ { fontSize: 12 } } variant="body2" color="#6b6b6b">
                  Créé le 21/02/22 par @elena
                </Typography>
              </CardContent>

            </CardActionArea> : type === 'evenement' ?
              <CardActionArea sx={ { minWidth: '170px', width: '18%', maxWidth: "200px",...style } }>
                { inlive === true ? inliveLogo : "" }

                <CardMedia
                  component="img"
                  height="200px"
                  image={ cardImage }
                  alt="Mat pokora"
                  sx={ { borderRadius: '11px', ...height, ...width } }
                />
                <MoreHorizIcon sx={ { position: "absolute", color: "#0058FF", top: "170px", right: "10px" } } />

                <CardContent sx={ { height: "auto", pr: 0, pl: 0 } }>
                  <Typography sx={ { fontSize: 14, fontWeight: '600', color: colors.cost.blue } } gutterBottom variant="h5" component="p">
                    12/05/2023 à 20:30
                    À Chaudron, Perpignan
                  </Typography>
                  <Typography sx={ { fontSize: 16, color: colors.cost.bluegray } } variant="body2" >
                    Only real fans
                  </Typography>
                  <Typography mt={ 1 } sx={ { fontSize: 14 } } variant="body2" color="#6b6b6b">
                    12 participants
                  </Typography>
                  <Typography mt={ 1 } sx={ { fontSize: 12 } } variant="body2" color="#6b6b6b">
                    Créé le 21/02/22 par @elena
                  </Typography>
                </CardContent>


              </CardActionArea> : type === "publication" ?
                <Box sx={ { padding: 2, backgroundColor: "white", width: "73%" } }>
                  <CardActionArea sx={ { borderRadius: "11px" } } >
                    <CardMedia
                      component="img"
                      height="200px"
                      image={ cardImage }
                      alt="Mat pokora"
                      sx={ { borderRadius: '11px', height: 200, width: "100%", ...height, ...width } }
                    />

                    <CardContent sx={ { width: "100%", height: "auto", pr: 0, pl: 0 } }>
                      <Typography mt={ 1 } mb={ 4 } sx={ { fontSize: 12 } } variant="body2" color="#6b6b6b">
                        Créé le 21/02/22 par @elena
                      </Typography>
                      <Typography sx={ { fontSize: 14, fontWeight: '600', color: colors.cost.blue } } gutterBottom variant="h5" component="p">
                        12/05/2023 à 20:30
                        À Chaudron, Perpignan
                      </Typography>

                      <Typography mt={ 1 } sx={ { fontSize: 14 } } variant="body2" color="#6b6b6b">
                        12 participants
                      </Typography>

                    </CardContent>

                  </CardActionArea>
                </Box> : type === 'amis' ?
                  <Box sx={ { width: "auto", gap:"5px" } }>
                    <CardActionArea sx={ { borderRadius: "11px" } } >
                      <CardMedia
                        component="img"
                        height="200px"
                        image={ imgSrc ? imgSrc : cardImage }
                        alt="Mat pokora"
                        sx={ { borderRadius: '11px', height: "200px", width: "200px", ...height, ...width } }
                      />

                      <CardContent sx={ { width: "100%", height: "auto", pr: 0, pl: 0, pt: 1 } }>

                        <Typography mt={ 0 } sx={ { fontSize: 16, fontWeight: "bold" } } variant="body2" color="#6b6b6b">
                          { pseudo ? pseudo : "Alberthoud05" }
                        </Typography>
                        <Typography mt={ 0 } sx={ { fontSize: 13 } } variant="body2" color="#6b6b6b">
                          <span><img src={ imgAmis ? imgAmis : '' } style={ { width: "15px", height: "14px" } } alt='' /> </span>    5 clubs en commun
                        </Typography>

                      </CardContent>

                    </CardActionArea>
                  </Box> : ""
      }

    </Card>
  );
}
