import { Grid } from '@mui/material'
import React from 'react'
import colors from '../../../constants/colors'
import FavoriteIcon from '@mui/icons-material/Favorite';

export default function PublicationFooter({
    totalComments,
    totalLikes,
    isSignal=false
}) {
  return (
    <Grid container xs={6} md={3} sm={4} lg={2} alignItems="center">
        <Grid item xs={6} >
            {!isSignal ?
                <i className="fa fa-heart" style={{marginRight: 5, color: colors.light.gray400, fontSize: 22}}></i>
            : 
                //   <span className='' style={ { color: colors.dark.secondary } }> Likes</span>
                <i className="fa fa-heart" style={{marginRight: 5, color: colors.light.gray400, fontSize: 22}}></i>
            }
              <span className='' style={ { color: colors.dark.secondary } }>{ totalLikes ? '('+totalLikes+')' : 12}</span>
        </Grid>
        <Grid item xs={6} alignItems="center">
        {!isSignal ?
                <i className="fa fa-comment" style={{marginRight: 5, color: colors.light.gray400, fontSize: 22}}></i>
            : 
            // <span className='' style={{color: colors.dark.secondary}}>Comments</span>
                  <i className="fa fa-comment" style={ { marginRight: 5, color: colors.light.gray400, fontSize: 22 } }></i>
            }
              <span className='' style={ { color: colors.dark.secondary } }>{ totalComments ? '('+totalComments+')' : 12}</span>
        </Grid>
    </Grid>
  )
}
