import { Grid } from '@mui/material'
import React from 'react'
import Item from '../../../components/Item'
import colors from '../../../constants/colors';
import CustomButton from '../../Button';

export default function PublicationHeader({
    isSignal = false,
    pseudo,
    avatar,
    created_at
}) {
    return (
        <Grid container>
            <Grid item xs={ 8 }>
                <Item imgSrc={ avatar } title={ pseudo ? pseudo : 'Carla sanchez' } subTitle={ created_at ? created_at :"il y a 1 an"} />
            </Grid>
            <Grid item ml={ 'auto' }>
                { !isSignal ?
                    <CustomButton text='Supprimer' btnStyle={ { color: 'red' } } variant="text" />
                    :
                    <>
                        <i className='fa fa-warning text-danger' style={ { marginRight: 8 } }></i>
                        <i className='fa fa-trash'></i>
                    </>
                }
            </Grid>
        </Grid>
    )
}
