
import * as React from "react";
import Box from "@mui/material/Box";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";
import Divider from "@mui/material/Divider";
import CustomButton from "../Button";
import { Checkbox, Container, Grid, Stack } from "@mui/material";
import { CancelOutlined } from "@mui/icons-material";

function Filter() {
	const [state, setState] = React.useState(false);

	const toggleDrawer = (open) => (event) => {
		if (
			event &&
			event.type === "keydown" &&
			(event.key === "Tab" || event.key === "Shift")
		) {
			return;
		}

		setState(open);
	};

	const list = () => (
		<Box
			sx={{ width: 400, pt: 2, fontSize: 12 }}
			role="presentation"
			// onClick={toggleDrawer(false)}
			onKeyDown={toggleDrawer(false)}
		>
            <Container>
                <Stack spacing={1}>
                    <Stack direction={'row'} justifyContent="space-between">
                        <Grid sx={{fontWeight: '400', fontSize: 12}}>Filter</Grid>
                        <Grid onClick={toggleDrawer(false)}><CancelOutlined /></Grid>
                    </Stack>
                    <Divider />
                    <Stack spacing={1}>
                        <Grid sx={{fontWeight: 'bold', fontSize: 14}}>FILTRER PAR</Grid>
                    </Stack>
                    
                    <Stack spacing={0}>
                        <Grid sx={{fontWeight: '600', fontSize: 12}}>Categories</Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Musique</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Danse</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Humour</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Medias</span></Grid>
                    </Stack>
                    <Divider />
                    <Stack spacing={0}>
                        <Grid sx={{fontWeight: '600', fontSize: 12}}>Types</Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Talent</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Fan</span></Grid>
                    </Stack>
                    <Divider />
                    <Stack spacing={0}>
                        <Grid sx={{fontWeight: '600', fontSize: 12}}>Badges</Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Bronze</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Argent</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Or</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Platine</span></Grid>
                        <Grid sx={{marginLeft: -1}}><Checkbox  size="small" sx={{'&.Mui-checked': {color: 'black'}}}/><span>Diamant</span></Grid>
                    </Stack>
                    <Divider />
                </Stack>
            </Container>
		</Box>
	);

	return (
		<div>
			<div
				style={{
					width: "fit-content",
					margin: "auto",
				}}
			>
					<React.Fragment>
                        <CustomButton text={ 'Filtrer' } variant='contained' btnStyle={ { color: 'white' } } onClick={toggleDrawer(true)} />
						<SwipeableDrawer
							anchor={'right'}
							open={state}
							onClose={toggleDrawer(false)}
							onOpen={toggleDrawer(true)}
						>
							{list()}
						</SwipeableDrawer>
					</React.Fragment>
			</div>
		</div>
	);
}

export default Filter;
