import React from 'react'
import Grid from '@mui/system/Unstable_Grid';
import Input from '../Inputs/IconInput/index';
import colors from '../../constants/colors';
import Button from '../Button'
import { useState } from 'react';
import { Typography } from '@mui/material';
import { color } from '@mui/system';

export default function Body({type, onClose}) {
    
  if(type==="boost"){
    
    return (<>
        <div style={{display: 'flex', justifyContent:'center', position:"relative"}}>
            <Input 
                placeholder="Saisir le montant du boost"
                style={{backgroundColor: "#cecece50", height:"30px", border: 'none', marginTop:"20px", paddingLeft:"20px", paddingRigth:"20px", color:"black", width: "250px", textAlign: 'center', fontSize:"12px"}}
                />
        </div>
        <div style={{ display:"flex", flexDirection:"row", marginTop:"10px", justifyContent:'center', position:"absolute", bottom: 35, left: "20%"}}>
        <Button text="Annuler" btnStyle={{ color: colors.cost.blue, backgroundColor:"white", width:"100px",heigth:2, marginRight:4}} onClick={onClose}></Button>
        <Button text="Confirmer" btnStyle={{ color: "white", backgroundColor: colors.cost.blue, width:"100px", heigth:2}}
        ></Button>
        </div>

        </>)
}else if (type==="relie"){
    return (<>
        <div style={{display: 'flex', flexDirection:"column", position:"relative"}}>
            <Typography sx={{ fontSize:"12px" }}>Nom</Typography>
            <Input 
                placeholder="Madonna"
                style={{backgroundColor: "#cecece50", height:"30px", border: 'none', marginTop:"10px", paddingLeft:"20px", paddingRigth:"20px", color:"black", width: "250px", textAlign: 'center', fontSize:"12px"}}
                />
        </div>
        <div style={{ display:"flex", flexDirection:"row", marginTop:"10px", justifyContent:'center', position:"absolute", bottom: 35, left: "20%"}}>
        <Button text="Annuler" btnStyle={{ color: colors.cost.blue, backgroundColor:"white", width:"100px",heigth:2, marginRight:4}} onClick={onClose}></Button>
        <Button text="Enregistrer" btnStyle={{ color: "white", backgroundColor: colors.cost.blue, width:"100px", heigth:2}}
        ></Button>
        </div>

        </>)
} else if (type==="modifie"){
    return (<>
        <div style={{display: 'flex', flexDirection:"column", position:"relative"}}>
            <Typography sx={{ fontSize:"14px"}}>Nom</Typography>
            <Input 
                placeholder="Madonna"
                style={{backgroundColor: "#cecece50", height:"30px", border: 'none', marginTop:"10px", paddingLeft:"20px", paddingRigth:"20px", color:"black", width: "250px", textAlign: 'center', fontSize:"12px"}}
                />
        </div>
        <div style={{ display:"flex", justifyContent: 'space-arround', flexDirection: 'row' }}>
            <Typography style={{ marginTop: "12px", fontSize:"12px"}}>Martin Salwig <span style={{ color:colors.dark.gray90,fontWeight:"bold" }}>125 clubs</span></Typography>
            <Typography style={{ marginTop: "12px", fontSize:"12px"}}>Club François <span style={{ color:colors.dark.gray90,fontWeight:"bold" }}>125 clubs</span></Typography>
        </div>
        <div style={{ display: 'block'}}>
            <Typography sx={{ fontSize:"14px", marginTop:"10px"}}>Catégorie</Typography>
            <Input 
                placeholder="Madonna"
                style={{backgroundColor: "#cecece50", height:"30px", border: 'none', marginTop:"10px", paddingLeft:"20px", paddingRigth:"20px", color:"black", width: "250px", textAlign: 'center', fontSize:"12px"}}
                />
        </div>

        </>)
}

}
