import { Avatar, Box, Stack } from '@mui/material'
import React from 'react'
import colors from '../../constants/colors'
import PropTypes from 'prop-types';

export default function index({
    imgSrc, imgStyle,
    title, titleStyle,
    subTitle, subTitleStyle, message,
    role, pseudo, signalement, adminSignalement,
    depuis, motif, signalementNew
}) {

  if(message && !signalement && !adminSignalement){
    return (
      <Box component={'div'} className="d-flex justify-content-start align-items-center">
          <Avatar sx={{ marginRight: 2, bgcolor: 'red', height: 50, width: 50, borderRadius:"60px", ...imgStyle }} variant="rounded" src={imgSrc}>
              {title ? title.split('')[0] : ''}
          </Avatar>
          <Stack justifyContent="space-evenly" sx={{ width: "100%"}}>
              <div style={{fontWeight: 'bold', fontSize: 14, ...titleStyle}}>{pseudo?pseudo:'User '} | <span style={{ fontWeight: '100', color: colors.light.gray90}}>{role?role:" Membre"}</span></div>
              <div style={{color: '#121412', fontSize: 14, ...subTitleStyle}}>{message?message:"Mon commentaire est celui-ci. je ne sais pas forcement quoi dire"}</div>
          </Stack>
      </Box>
    )
  }else if(signalement){
    return(
      <Box component={'div'} className="d-flex justify-content-start align-items-center">
        <Avatar sx={{ marginRight: 2, bgcolor: 'red', height: 50, width: 50, borderRadius:"60px", ...imgStyle }} variant="rounded" src={imgSrc}>
            {title ? title.split('')[0] : ''}
        </Avatar>
        <Stack justifyContent="space-evenly" sx={{ width: "250px"}}>
            <div style={{fontWeight: 'bold', fontSize: 14, ...titleStyle}}>{pseudo?pseudo:'User '}</div>
            <div style={{color: colors.cost.danger, fontWeight: 'bold', fontSize: 14, ...subTitleStyle}}>{signalement?signalement+" signalement(s)":"Aucun signalement"}</div>
        </Stack>
      </Box>
    )
  } else if( adminSignalement ){
    if(signalementNew===true){
      return(
        <Box >
          <Box component={'div'} className="d-flex justify-content-start align-items-center">
              <Avatar sx={{ marginRight: 2, bgcolor: 'red', height: 50, width: 50, borderRadius:"60px", ...imgStyle }} variant="rounded" src={imgSrc}>
                  {title ? title.split('')[0] : ''}
              </Avatar>
              <Stack justifyContent="space-evenly" sx={{ width: "250px"}}>
                  <div style={{color:'#494949', fontWeight: 'bold', fontSize: 14, ...titleStyle}}>{pseudo?pseudo:'User '}</div>
                  <div style={{color: colors.light.gray90 , fontSize: 14, ...subTitleStyle}}>{depuis?depuis:"2j"}</div>
              </Stack>
          </Box>
          <Box>
            <div style={{color: '#6C6C6C', fontWeight:"bold", paddingTop:"10px", fontSize: 14, ...subTitleStyle}}>{motif?motif:"Incitation/discour haineux"}</div>
            <div style={{color: '#6C6C6C', fontSize: 14, ...subTitleStyle}}>{message?message:"Mon commentaire est celui-ci. je ne sais pas forcement quoi dire"}</div>
          </Box>
        </Box>
      )
    }else{
      return(
        <Box>
          <Box component={'div'} className="d-flex justify-content-start align-items-center">
              <Avatar sx={{ marginRight: 2, bgcolor: 'red', height: 50, width: 50, borderRadius:"60px", ...imgStyle }} variant="rounded" src={imgSrc}>
                  {title ? title.split('')[0] : ''}
              </Avatar>
              <Stack justifyContent="space-evenly" sx={{ width: "250px"}}>
                  <div style={{color:'#494949', fontWeight: 'bold', fontSize: 14, ...titleStyle}}>{pseudo?pseudo:'User '}</div>
                  <div style={{color: colors.light.gray90 , fontSize: 14, ...subTitleStyle}}>{depuis?depuis:"2j"}</div>
              </Stack>
          </Box>
          <Box>
            <div style={{color: '#6C6C6C', fontWeight:"bold", paddingTop:"10px", fontSize: 14, ...subTitleStyle}}>{motif?motif:"Incitation/discour haineux"}</div>
            <div style={{color: '#6C6C6C', fontSize: 14, ...subTitleStyle}}>{message?message:"Mon commentaire est celui-ci. je ne sais pas forcement quoi dire"}</div>
          </Box>
        </Box>
      )
    }
  }
  else{
    return (
      <Box component={'div'} className="d-flex justify-content-start align-items-center">
          <Avatar sx={{ marginRight: 2, bgcolor: 'red', height: 50, width: 50, ...imgStyle }} variant="rounded" src={imgSrc}>
              {title ? title.split('')[0] : ''}
          </Avatar>
          <Stack justifyContent="space-evenly">
              <div style={{fontWeight: 'bold', fontSize: 14, ...titleStyle}}>{title}</div>
              <div style={{fontWeight: 'bold', color: colors.dark.secondary, fontSize: 12, ...subTitleStyle}}>{subTitle}</div>
          </Stack>
      </Box>
    )
  }
  
}

index.propTypes = {
    imgSrc: PropTypes.string,
    title: PropTypes.string,
    imgStyle: PropTypes.object,
    subTitle: PropTypes.string,
    subTitleStyle: PropTypes.object,
    titleStyle: PropTypes.object,
  };
  