import { Box, Grid, Typography } from '@mui/material';
import React, { useEffect, useState } from 'react';
import Profile from '../../../../components/Card/Profile';
import Info from '../../../../components/Card/Infos'
import colors from '../../../../constants/colors';
import { useParams } from 'react-router-dom';
import CustomButton from '../../../../components/Button';


const Infos = ({ rows, windowHeight }) => {
    const params = useParams()
    const id = params.id - 1
    return (
        <>
            <Grid container spacing={ 3 }>
                <Grid item xs={ 6 } md={ 8 }>
                    <Profile typelecture={ 'lecture' } profileInfos={ rows[id] } />
                </Grid>
                <Grid item xs={ 6 } md={ 4 } sx={ {
                    maxHeight: `${windowHeight - 140}px`, position: 'relative', width: '100%',
                    overflowY: 'scroll',
                    '&::-webkit-scrollbar': {
                        display: 'none',
                    },
                } }>
                    <Info
                        imgsrc={ rows[id].image }
                        name={ rows[id].pseudo }
                        depuis={ rows[id].createdAt }
                        style={ { bgcolor: 'white', borderRadius: '5px', boxShadow: '1', width: '100%' } }
                    />
                    <Box sx={ { marginTop: 3, borderRadius: '5px', boxShadow: '1', width: '100%', bgcolor: 'white' } } >
                        <Box sx={ { mt: 2, marginLeft: '10px', marginRight: "10px", p: 2, display: "flex", justifyContent: "space-between" } }>
                            <Typography sx={ { fontWeight: "bold", fontSize: 14, p: 1 } }>Role utilisateur</Typography>
                            <Typography sx={ { fontSize: 14, fontWeight: 'bold' } } color="#7f8b99" gutterBottom>
                                <CustomButton text='Modifier' btnStyle={ { color: '#0058ff' } } variant="text" />
                            </Typography>
                        </Box>
                        <Box sx={ { marginLeft: '10px', marginRight: "10px", p: 2 } }>
                            <Typography sx={ { fontSize: 14, fontWeight: 'bold' } } color="#7f8b99" gutterBottom>
                                { rows[id].role }
                            </Typography>
                        </Box>
                    </Box>
                    <Box sx={ { marginTop: 3, borderRadius: '5px', boxShadow: '1', width: '100%', bgcolor: 'white' } } >
                        <Box sx={ { mt: 2, marginLeft: '10px', marginRight: "10px", p: 2, display: "flex", justifyContent: "space-between" } }>
                            <Typography sx={ { fontWeight: "bold", fontSize: 14, p: 1 } }>Confidentialité</Typography>
                            <Typography sx={ { fontSize: 14, fontWeight: 'bold' } } color="#7f8b99" gutterBottom>
                                <CustomButton text='Modifier' btnStyle={ { color: '#0058ff' } } variant="text" />
                            </Typography>
                        </Box>
                        <Box sx={ { marginLeft: '10px', marginRight: "10px", p: 2 } }>
                            <Typography sx={ { fontSize: 14, fontWeight: 'bold' } } color="#7f8b99" gutterBottom>
                                Compte privé
                            </Typography>
                        </Box>
                    </Box>
                    <Box sx={ { marginTop: 3, borderRadius: '5px', boxShadow: '1', width: '100%', bgcolor: 'white' } } >
                        <Box sx={ { mt: 2, marginLeft: '10px', marginRight: "10px", p: 2, display: "flex", justifyContent: "space-between" } }>
                            <Typography sx={ { fontWeight: "bold", fontSize: 14, p: 1 } }>Centre d'interêt</Typography>
                            <Typography sx={ { fontSize: 14, fontWeight: 'bold' } } color="#7f8b99" gutterBottom>
                                <CustomButton text='Modifier' btnStyle={ { color: '#0058ff' } } variant="text" />
                            </Typography>
                        </Box>
                        <Box sx={ { marginLeft: '10px', marginRight: "10px", p: 2 } }>
                            <Box sx={{ maxWidth: '150px' }}>
                                <Typography sx={ { borderRadius: '10px', display: 'flex', justifyContent: 'space-between', width: 'auto', fontWeight: "bold", fontSize: 14, padding: 1, bgcolor: colors.light.gray200 } } color="#7f8b99" gutterBottom>
                                    #Musique
                                    <CustomButton text='X' btnStyle={ { color: '#0058ff', height: '20px', width: '20px' } } variant="text" />
                                </Typography>
                                <Typography sx={ { borderRadius: '10px', display: 'flex', justifyContent: 'space-between', width: 'auto', fontWeight: "bold", fontSize: 14, padding: 1, bgcolor: colors.light.gray200 } } color="#7f8b99" gutterBottom>
                                    #pop
                                    <CustomButton text='X' btnStyle={ { color: '#0058ff', height: '20px', width: '20px' } } variant="text" />
                                </Typography>
                            </Box>
                        </Box>
                    </Box>
                </Grid>
            </Grid>
        </>
    );
};

export default Infos;