import { Grid, Stack } from '@mui/material'
import React from 'react'
import colors from '../../constants/colors'
import CustomButton from '../Button'
import InputWithIcon from '../Inputs/IconInput'
import Tab from '../Tab'

export default function index({
    title,
    subtitle,
    onSearchHandle,
    tabElement = null
}) {
    return (
        <Stack spacing={ 2 }>
            <Stack direction="row" alignItems={ 'center' } justifyContent="space-between">
                <Stack direction={ 'row' } alignItems={ 'center' }>
                    <h2 style={ { color: colors.dark.black, fontWeight: 'bold' } }>{ title }</h2>
                    <span style={ { color: colors.light.gray400, marginTop: 4, marginLeft: 4, fontWeight: 'bold' } }>{ subtitle }</span>
                </Stack>
                <Grid>
                    <InputWithIcon style={ { border: 'none' } } hasIcon={ true } onChange={ onSearchHandle } placeholder="Rechercher" />
                </Grid>
            </Stack>
            { tabElement &&
                <Stack>
                    <Tab tabElement={ tabElement } />
                </Stack>
            }
        </Stack>
    )
}
