import { Box } from '@mui/material';
import React from 'react';
import Image from '../../../../components/Card/Image';

const albums = [
    '1', '2', '3', '1', '1', '2', '3', '1', '2', '3', '1', '1', '2', '3'
]

const Albums = ({ windowHeight }) => {
    return (
        <Box sx={ {
            maxHeight: `${windowHeight - 160}px`, position: 'relative',
            overflowY: 'scroll',
            '&::-webkit-scrollbar': {
                display: 'none',
            },
        } }>
            <Box display="flex" flexDirection="row" flexWrap="wrap" gap={ 2 } justifyContent='space-between'>
                { albums.map((user) => (
                    <Box key={ user.id }>
                        <Image type={'album'}  imgAmis={ 'https://i.pravatar.cc/150?img=5' } />
                    </Box>
                )) }
            </Box>
        </Box>
    );
};

export default Albums;