export default function CLubs() {
    const [windowSize, setWindowSize] = useState({
        width: window.innerWidth,
        height: window.innerHeight,
    });

    // Just replace the tabContent by the component
    const content = [
        { tabTitle: "Infos", tabContent: <Infos rows={ rows } windowHeight={ windowSize.height } /> },
        { tabTitle: "Publications", tabContent: <Publication windowHeight={ windowSize.height } /> },
        { tabTitle: "Membre", tabContent: <Member /> },
        { tabTitle: "Albums", tabContent: <Albums windowHeight={ windowSize.height } /> },
        { tabTitle: "Scenes", tabContent: <Scenes /> },
        { tabTitle: "Evenements", tabContent: <Events type='' windowHeight={ windowSize.height } windowWidth={ windowSize.width } /> },
        { tabTitle: "Go inlive", tabContent: <GoInlive type='' windowHeight={ windowSize.height } windowWidth={ windowSize.width } /> },
        { tabTitle: "Badge", tabContent: <Badges rows={ [] } windowHeight={ windowSize.height } /> },
        { tabTitle: "Signalements", tabContent: <Reports windowHeight={ windowSize.height } /> },
    ]

    useEffect(() => {
        function handleResize() {
            setWindowSize({
                width: window.innerWidth,
                height: window.innerHeight,
            });
        }
        window.addEventListener('resize', handleResize);
        return () => window.removeEventListener('resize', handleResize);
    }, []);
    return (
        <Box>
            <PageHeader title={ 'Clubs' } subtitle={ '20 clubs' } tabElement={ content } />
        </Box>
    )
}