import React from 'react'
import {Box, Grid} from '@mui/material';
import Typography from '@mui/material/Typography';
import { Button, CardActionArea, CardActions } from '@mui/material';
import cardImage from "../../Images/media.jpg"
import LinkIcon from '@mui/icons-material/Link';
import MoreHorizIcon from '@mui/icons-material/MoreHoriz';
import colors from '../../constants/colors'
import EmailIcon from '@mui/icons-material/Email';


export default function Infos({ imgsrc, imgStyle, style,
  membreDep, bpm, right, name, depuis
}) {
  if(!right){
    return (
      <Box sx={{ padding: 2, paddingTop: 3, width: "20vw",
      display:"flex", alignContent:"center", flexDirection:"column", ...style}}>
          <Box sx={{ display: "flex", flexDirection: "column"}}>
              <Box sx={{width: 120, height: 120, backgroundColor: "gray",
               borderRadius: "11px", mb:2, marginLeft:'auto', marginRight:"auto", textAlign:'center', overflow: 'hidden'}}>
                  <img src={imgsrc} alt="" style={{ display:"block", borderRadius: "11px", margin:'auto', ...imgStyle}} />
              </Box>
              <Box sx={{mt:2, marginLeft:'auto', marginRight:"auto"}}>
                  <Typography sx={{mb:1, fontSize: "14px", fontWeight: "bold", fontSize:"16px", textAlign:"center"}}>{name?name:"leilaaaaand"}</Typography>
                  <Typography sx={{fontSize: "14px", fontSize:"16px", textAlign:"center"}}>{bpm?bpm:"570 BPM"}</Typography>
                  <Typography sx={{fontSize: "14px"}}><span style={{ fontWeight: "bold" }}>Inlivers depuis le: </span>{depuis?depuis:"10/12/22"}</Typography>
                  {/* <Typography sx={{fontSize: "14px"}}><span style={{ fontWeight: "bold" }}>Membre depuis: </span>23/01/23</Typography> */}
              </Box>
          </Box>
          <Box sx={{ display: "flex", flexDirection:"row", gap: "5px"}}>
            <Box sx={{ color: colors.cost.blue, backgroundColor: "#0085f91c", mt:1, marginLeft:'auto', marginRight:"auto", width:"100%",
              padding:"5px 8px", textAlign:"center", borderRadius:"4px", fontWeight:"bold", fontSize:"14px", cursor:"pointer" }}>Voir la fiche utilisateur
            </Box>
            <span style={{ color: colors.cost.blue, cursor:"pointer", backgroundColor: "#0085f91c", mt:1, marginLeft:'auto', marginRight:"auto", width:"auto",
              padding:"2px 4px", textAlign:"center", borderRadius:"4px", fontWeight:"bold", fontSize:"14px", marginTop:"8px", alignItems:"center", lineHeight:"25px" }}><EmailIcon sx={{ color: colors.cost.blue}}/></span>
          </Box>
          
          <Box sx={{ color: colors.cost.reddanger, backgroundColor: "#ff002218", width:"100%",
           padding:"5px 8px", textAlign:"center", borderRadius:"4px", fontWeight:"bold", mt:"12px", fontSize:"14px", cursor:"pointer"}}>Bannir du club</Box>
      </Box>
    )
  }else{
    return (
      <Box sx={{ padding: 2, paddingTop: 3, minWidth: "20vw", width:"23vw",
      display:"flex", alignContent:"center", flexDirection:"column", ...style}}>
          <Box sx={{ display: "flex", flexDirection: "row"}}>
              <Box sx={{minWidth: 120, minHeight: 120,
               borderRadius: "11px", marginRight: 4, marginLeft:'auto', marginRight:"auto", overflow:"hidden"}}>
                <img src={ imgsrc } alt="" style={{ borderRadius: "11px", height:"9vw", width:"9vw" }} />
              </Box>
              <Box sx={{mt:2, marginLeft:'auto', marginRight:"auto", marginLeft:"10px"}}>
                  <Typography sx={{mb:2, fontSize: "14px", fontWeight: "bold", fontSize:"16px"}}>{name?name:"leilaaaaand"}</Typography>
                  <Typography sx={{fontSize: "14px", fontSize:"16px"}}>{bpm?bpm:"570 BPM"}</Typography>
                  <Typography sx={{fontSize: "14px"}}><span style={{ fontWeight: "bold" }}>Inlivers depuis le: </span>{depuis?depuis:"10/12/22"}</Typography>
                  {/* <Typography sx={{fontSize: "14px"}}><span style={{ fontWeight: "bold" }}>Membre depuis: </span>23/01/23</Typography> */}
              </Box>
          </Box>
          <Box sx={{ color: colors.cost.blue, backgroundColor: "#0085f91c", mt:1, marginLeft:'auto', marginRight:"auto", width:"100%",
        padding:"5px 8px", textAlign:"center", borderRadius:"6px", fontWeight:"bold", fontSize:"14px" }}>Voir la fiche utilisateur <span style={{ backgroundColor:"0085f91c"}}></span></Box>
          <Box sx={{ color: colors.cost.reddanger, backgroundColor: "#ff002218", width:"100%",
           padding:"5px 8px", textAlign:"center", borderRadius:"6px", fontWeight:"bold", mt:"12px", fontSize:"14px"}}>Bannir du club</Box>
      </Box>
    )
  }
  
}
