const Publication = ({ windowHeight }) => {

    const tabElement = [
        { tabTitle: "Likes", tabContent: <Likes /> },
        { tabTitle: "Commentaires", tabContent: <Comments /> },
    ]

    return (
        <>
            <Grid container spacing={ 3 }>
                <Grid item xs={ 6 } md={ 8 } sx={ {
                    maxHeight: `${windowHeight - 150}px`, position: 'relative',
                    overflowY: 'scroll',
                    '&::-webkit-scrollbar': {
                        display: 'none',
                    },
                } }>
                    {
                        pubs.map((item) => (
                            <Box sx={ { marginTop: 3, borderRadius: '5px', boxShadow: '1', width: '100%', bgcolor: 'white', p: 2 } } >
                                <Pub
                                    avatar={ item.avatar }
                                    contentPub={ item.content_publication }
                                    created_at={ item.created_at }
                                    isSignal={ false }
                                    medias={ item.media }
                                    totalComments={ item.total_comments }
                                    pseudo={ item.pseudo }
                                    totalLikes={ item.total_likes }
                                />
                            </Box>
                        ))
                    }
                </Grid>
                <Grid item xs={ 6 } md={ 4 } sx={ {
                    maxHeight: `${windowHeight - 150}px`, position: 'relative',
                    overflowY: 'scroll',
                    '&::-webkit-scrollbar': {
                        display: 'none',
                    },
                } }>
                    <Item>
                        { tabElement &&
                            <Stack sx={ { paddingTop: 3 } }>
                                <Tab tabElement={ tabElement } />
                            </Stack>
                        }
                    </Item>
                </Grid>
            </Grid>
        </>
    );
};

export default Publication;