const Member = ({
    filterText = "Filtrer",
    filterHandle,
    secondHandleText = "Exporter",
    secondHandle, }) => {
    const navigate = useNavigate()
    return (
        <>
            <Stack direction="row" alignItems={ 'center' } justifyContent="space-between">
                <Grid sx={ { color: 'red' } }>
                    <CustomButton text={ filterText } variant='contained' btnStyle={ { color: 'white' } } onClick={ filterHandle } />
                </Grid>
                <Grid>
                    <CustomButton text={ secondHandleText } variant='contained' btnStyle={ { color: colors.dark.primary, backgroundColor: 'white' } } onClick={ secondHandle } />
                </Grid>
            </Stack>
            <TableContainer component={ Paper } sx={ { marginTop: 2 } }>
                <Table sx={ { minWidth: 650 } } aria-label="caption table">
                    <TableHead style={ { backgroundColor: colors.light.gray200 } }>
                        <TableRow>
                            <TableCell><Checkbox aria-label='Select All' /></TableCell>
                            <TableCell>Pseudo<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Role<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Dons<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Publi<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Com<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Like<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Membre depuis<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left"></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        { rows.map((row, index) => (
                            <TableRow key={ row.pseudo } sx={ { "&:hover": { bgcolor: colors.light.gray100, cursor: "pointer" } } }>
                                <TableCell component="th" scope="row">
                                    {/* { index += 1 } */ }
                                    <Checkbox aria-label={ row.pseudo } />
                                </TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">
                                    <Item title={ row.pseudo } titleStyle={ { fontWeight: '400' } } />
                                </TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">{ row.role }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">{ row.dons }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">{ row.publications }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">{ row.commentaires }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">{ row.likes }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left">{ row.created_at }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + row.id) } } align="left"></TableCell>
                            </TableRow>
                        )) }
                    </TableBody>
                </Table>
            </TableContainer>
            <Box direction="row" alignItems={ 'center' } justifyContent="end" sx={ { display: "flex", paddingTop: 3, paddingBottom: 3 } }>
                <Pagination count={ rows.length } color="primary" shape="rounded" />
            </Box>
        </>
    );
};

export default Member;