import * as React from 'react';
import ListSubheader from '@mui/material/ListSubheader';
import List from '@mui/material/List';
import ListItemButton from '@mui/material/ListItemButton';
import Collapse from '@mui/material/Collapse';
import Item from '../Item'
import { Box } from '@mui/system';
import colors from '../../constants/colors'
import Button from '../../components/Button'
import Modal from "../../components/Modal"
import { ListItemIcon } from '@mui/material';
import { StarBorder } from '@mui/icons-material';
import ListItemText from '@mui/material/ListItemText';
import Fade from '@mui/material/Fade';


export default function  NestedList({type, rows}) {
  const [open, setOpen] = React.useState(false);
  const [collapseOpen, setCollapseOpen] = React.useState(false);
  const [modalTitle, setModalTitle]= React.useState(false);
  const [modalDescription, setModalDescription]= React.useState(false);
  const [modalType, setModalType]= React.useState(false);

  const handleClick = () => {
    setOpen(!open);
  };
  const toggleCollapse = () => {
    setCollapseOpen(!collapseOpen);
  };
  const closeModel = () => {
    setOpen(false);
  };

  const ignoreClick = (row) => {
    setOpen(!open);
    setModalTitle("Ignorer le signalement");
    setModalDescription("En ingnorant ce signalement "+row.pseudo+" restera en ligne");
    setModalType('alert');
  };

  const approuveClick = (row) => {
    setOpen(!open);
    setModalTitle("Approuver le signalement");
    setModalDescription("En approuvant ce signalement "+ row.pseudo+" ne pourra plus se connecter sur son compte");
    setModalType('alert');
  };

  const avertissementClick = (row) => {
    setOpen(!open);
    setModalTitle("Avertissement");
    setModalDescription("En envoyant un avertissement, "+ row.pseudo +" recevra une notification d'alerte mais pourra de nouveau  se connecter à son compte");
    setModalType('alert');

  };

  const showModal = () =>{
    return true
  }


  const finalRow= []
  rows.forEach(row=>{
    finalRow.push(
      row.signalementNew?
      <ListItemButton onClick={toggleCollapse} classe="itemClasse" sx={{ position: "relative", bgcolor:"#DEEDF1", width: "100%", pl:3, pt:2}}>
        <Item imgSrc={row.image} imgStyle={{ borderRadius:'6px' }} message={row.comment}
        pseudo={row.pseudo} role={row.role} signalement={row.signalement}/>
        <Box sx={{ display:"flex", position:'absolute', gap:"8px",
          width: "300px", right:"15px"}}>
          <Button text="Ignorer" onClick={()=>ignoreClick(row)} value={open} btnStyle={{ color:colors.cost.blue, backgroundColor:"transparent", paddingTop:" 3px", borderColor:"transparent", height: "30px", }}/>
          <Button text="Approuver" onClick={()=>approuveClick(row)} btnStyle={{ color:colors.cost.blue, backgroundColor:"white", paddingTop:" 3px", borderColor:"white", height: "30px" }}/>
          <Button text="Avertir" onClick={()=>avertissementClick(row)} btnStyle={{ color:"white", backgroundColor: colors.cost.blue, paddingTop:"3px", height: "30px", lineHeight:"30px"}}/>
        </Box>
      </ListItemButton>:
      <ListItemButton classe="itemClasse" sx={{ position: "relative", pl:3}}>
        <Item imgSrc={row.image} imgStyle={{ borderRadius:'6px' }} message={row.comment}
        pseudo={row.pseudo} role={row.role} signalement={row.signalement}/>
        <Box sx={{ color: colors.cost.blue, fontSize: "12px",
          position:"absolute", right:"20px"}}>Avertissement le {row.last_comment}</Box>
      </ListItemButton>
      )

    if(row.reponse){
      // <Collapse in={collapseOpen} timeout="auto" unmountOnExit sx={{ borderLeft: 'solid', borderColor: "#d7dbec", borderWidth: 2, ml: 4, display:'none'}}>
      //   <List component="div" disablePadding >
      //     {finalRow.push(
      //       row.reponse.forEach(response=>{
      //         finalRow.push(
      //           <ListItemButton sx={{ pl: 9, pt:2, pb:1, bgcolor:"#DEEDF1" }}>
      //             <Item imgSrc={response.image} adminSignalement={true} signalementNew={true} imgStyle={{ borderRadius:'6px' }} message={response.comment}
      //               pseudo={response.pseudo} role={response.role} />
      //           </ListItemButton>
      //             )
      //         })            
      //     )}
      //   </List>
      // </Collapse>
      {finalRow.push(<Collapse  in={collapseOpen} sx={{ borderLeft: 'solid', borderColor: "#d7dbec", borderWidth: 2}}>              
                  <ListItemButton sx={{ pl: 9, pt:2, pb:1, bgcolor:"#DEEDF1" }}>
                    <Item imgSrc={row.image} adminSignalement={true} signalementNew={true} imgStyle={{ borderRadius:'6px' }} message={row.reponse?.comment}
                      pseudo={row.reponse?.pseudo} role={row.reponse?.role} />
                  </ListItemButton>
        </Collapse>)}
    }

  })


  if( type==="signalement"){
    return (
      <List
        className='moi'
        sx={{ width: '100%', minWidth: 500, mt:2,
         bgcolor: 'background.paper' }}
        component="nav"
        aria-labelledby="nested-list-subheader"
        subheader={
          <ListSubheader component="div" id="nested-list-subheader">
            {/* Nested List Items */}
      </ListSubheader>
        }
      >
        {finalRow}
        {<Modal showModal={open} title={modalTitle} description={modalDescription} type={modalType} onClose={closeModel} />}

      </List>
    );
  }else{
    return (
      <List
        sx={ { width: '100%', margin: 0 } }
        component="nav"
      >
        <ListItemButton>
          <Item
            message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
            pseudo={ "Carla" }
            title="WEoinoiwe on"
            depuis={ 'wefwef' }
            imgStyle={ { borderRadius: "5px" } }
            imgSrc={ "https://i.pravatar.cc/150?img=9" }
          />
          {/* <ListItemText primary="Sent mail" />  */ }
        </ListItemButton>
        <ListItemButton>
          <Item
            message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
            pseudo={ "Audrey" }
            title="WEoinoiwe on"
            depuis={ 'wefwef' }
            imgStyle={ { borderRadius: "5px" } }
            imgSrc={ "https://i.pravatar.cc/150?img=9" }
          />
        </ListItemButton>
        <ListItemButton onClick={ handleClick }>
          <Item
            message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
            pseudo={ "Carla" }
            title="WEoinoiwe on"
            depuis={ 'wefwef' }
            imgStyle={ { borderRadius: "5px" } }
            imgSrc={ "https://i.pravatar.cc/150?img=9" }
          />
        </ListItemButton>
        {/* <Collapse in={open} timeout="auto" unmountOnExit> */ }
        <Collapse in={ true } timeout="auto" unmountOnExit sx={ { borderLeft: 'solid', borderColor: "#d7dbec", borderWidth: 2, ml: 4 } }>
          <List component="div" disablePadding>
            <ListItemButton sx={ { pl: 4 } }>
              <Item
                message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
                pseudo={ "Carla" }
                title="WEoinoiwe on"
                depuis={ 'wefwef' }
                imgStyle={ { borderRadius: "5px" } }
                imgSrc={ "https://i.pravatar.cc/150?img=9" }
              />
            </ListItemButton>
            <ListItemButton sx={ { pl: 4 } }>
              <Item
                message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
                pseudo={ "Carla" }
                title="WEoinoiwe on"
                depuis={ 'wefwef' }
                imgStyle={ { borderRadius: "5px" } }
                imgSrc={ "https://i.pravatar.cc/150?img=9" }
              />
            </ListItemButton>
            <ListItemButton sx={ { pl: 4 } }>
               <Item
            message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
            pseudo={ "Carla" }
            title="WEoinoiwe on"
            depuis={ 'wefwef' }
            imgStyle={ { borderRadius: "5px" } }
            imgSrc={ "https://i.pravatar.cc/150?img=9" }
          />
            </ListItemButton>

          </List>
        </Collapse>
        <ListItemButton onClick={ handleClick }>
           <Item
            message={ "Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever  " }
            pseudo={ "Carla" }
            title="WEoinoiwe on"
            depuis={ 'wefwef' }
            imgStyle={ { borderRadius: "5px" } }
            imgSrc={ "https://i.pravatar.cc/150?img=9" }
          />
        </ListItemButton>
      </List>
    );
  }

  
}