import { Box } from '@mui/material';
import React from 'react';
import Comment from '../../../../../components/Comment'

const Comments = () => {
    return (
        <>
            <Box sx={ {  width: '100%' } } >
                <Comment   type={ 'comment' } rows={ ['1', '1', '1'] } />
            </Box>
        </>
    );
};

export default Comments;