const Users = () => {

    const [windowSize, setWindowSize] = useState({
        width: window.innerWidth,
        height: window.innerHeight,
    });

    const params = useParams()
    const id = params.id - 1

    const tabElement = [
        { tabTitle: "Infos générales", tabContent: <Infos rows={ rows } windowHeight={ windowSize.height } /> },
        { tabTitle: "Club gérés", tabContent: <Clubs category={ 'gere' } windowHeight={ windowSize.height } /> },
        { tabTitle: "Club suivis", tabContent: <Clubs category={ 'suivis' } windowHeight={ windowSize.height } /> },
        { tabTitle: "Amis", tabContent: <Friends rows={ rows } windowHeight={ windowSize.height } /> },
        { tabTitle: "Badges", tabContent: <Badges rows={ rows } windowHeight={ windowSize.height } /> },
        { tabTitle: "Signalements reçu", tabContent: <Reports title={ "Signalements reçu" } windowHeight={ windowSize.height } /> },
        { tabTitle: "Signalements envoyés", tabContent: <Reports title={ "Signalements envoyés" } windowHeight={ windowSize.height } /> },
    ]

    return (
        <>
            <PageHeader title={ rows[id].pseudo } subtitle={ '' } tabElement={ tabElement } />
        </>
    );
};

export default Users;