import { Box, Stack } from '@mui/material'
import React from 'react'
import Header from './Header';
import Footer from './Footer';
import Body from './Body';

export default function index({
  pseudo = null,
  avatar = null,
  created_at = null,
  isSignal = false,
  contentPub = '',
  medias = null,
  totalComments = null,
  totalLikes = null

}) {
  return (
    <Box>
      <Stack spacing={ 2 }>
        <Header avatar={ avatar } pseudo={ pseudo } created_at={ created_at } isSignal={ isSignal } />
        <Body contentPub={ contentPub } medias={ medias } />
        <Footer totalComments={ totalComments } totalLikes={ totalLikes } />
      </Stack>
    </Box>
  )
}
