import { Avatar, Typography } from '@mui/material';
import { Box } from '@mui/system';
import React from 'react';
import colors from '../../constants/colors';

const ChatItem = ({ avatar, avatarStyle, content, date, pseudo }) => {
    return (
        <Box sx={ { width: '100%', display: 'flex', p: 2 } }>
            <Avatar
                alt={ pseudo ? pseudo : '' }
                src={ avatar ? avatar : '' }
                sx={ { marginRight: 2, bgcolor: 'red', height: 50, width: 50, borderRadius: '60px', ...avatarStyle } }
            />
            <Box>
                <Box sx={ { p: 3, bgcolor: colors.light.gray100, borderRadius: '0 20px 20px 20px' } }>
                    <Typography>
                        { content ? content : '' }
                    </Typography>
                </Box>
                <Box sx={ { marginLeft: 2, marginTop: 1 } }>
                    <Typography>
                        { date ? date : '' }
                    </Typography>
                </Box>
            </Box>
        </Box>
    );
};

export default ChatItem;