import { Box, Checkbox, Grid, IconButton, Paper, Stack, Table, TableBody, TableCell, TableContainer, TableHead, TableRow } from '@mui/material';
import React from 'react';
import colors from '../../../../constants/colors';
import DeleteIcon from '@mui/icons-material/Delete';
import EditIcon from '@mui/icons-material/Edit';
import Modal from "../../../../components/Modal"

const rows = [
    {
        id: 1,
        name: 'Scene principale',
        link_with: 'Pyramide',
        confidentiality: 'Privé'
    },
    {
        id: 2,
        name: 'Scene secondaire',
        link_with: 'Sphère',
        confidentiality: 'Public'
    },
    {
        id: 3,
        name: 'Scène de test',
        link_with: 'Cube',
        confidentiality: 'Privé'
    },
    {
        id: 4,
        name: 'Scène d\'accueil',
        link_with: 'Cylindre',
        confidentiality: 'Public'
    },
    {
        id: 5,
        name: 'Scène finale',
        link_with: 'Torus',
        confidentiality: 'Privé'
    },
    {
        id: 6,
        name: 'Scène alternative',
        link_with: 'Cône',
        confidentiality: 'Public'
    },
    {
        id: 7,
        name: 'Scène expérimentale',
        link_with: 'Octaèdre',
        confidentiality: 'Privé'
    },
    {
        id: 8,
        name: 'Scène de démonstration',
        link_with: 'Dodecaèdre',
        confidentiality: 'Public'
    },

]

const Scenes = () => {
    
    const [open, setOpen] = React.useState(false);
    const [modalTitle, setModalTitle]= React.useState(false);
    const [modalDescription, setModalDescription]= React.useState(false);
    const [modalType, setModalType]= React.useState(false);

    const handleClick = (row) => {
        setOpen(!open);
        setModalTitle("Ignorer le signalement");
        setModalDescription("En ingnorant ce signalement "+row.name+" restera en ligne");
        setModalType('alert');
      }
      const handleDeleteClick = (row) => {
        setOpen(!open);
        setModalTitle("Suppimer la scene");
        setModalDescription("Voulez vous vraiment supprimer "+row.name+" ?");
        setModalType('alert');
      }
      const handleEditClick = (row) => {
        setOpen(!open);
        setModalTitle("Modifier la scene");
        setModalDescription("Voulez vous vraiment Modifier "+row.name+" ?");
        setModalType('alert');
      }
    const closeModel = () => {
    setOpen(false);
    };

    return (
        <>
            <Box>
                <Box sx={ { paddingTop: 3, paddingBottom: 3 } }>
                    <TableContainer component={ Paper } sx={ { marginTop: 2 } }>
                        <Table sx={ { minWidth: 650 } } aria-label="caption table">
                            <TableHead style={ { backgroundColor: colors.light.gray200 } }>
                                <TableRow>
                                    <TableCell><Checkbox aria-label='Select All' /></TableCell>
                                    <TableCell>Nom<i className='fa fa-sort-up'></i></TableCell>
                                    <TableCell align="left">Lié à<i className='fa fa-sort-up'></i></TableCell>
                                    <TableCell align="left">Confidentialité<i className='fa fa-sort-up'></i></TableCell>
                                    <TableCell align="left"></TableCell>
                                </TableRow>
                            </TableHead>
                            <TableBody>
                                { rows.map((row) => (
                                    <TableRow key={ row.name } sx={ { "&:hover": { bgcolor: colors.light.gray100, cursor: "pointer" } } }>
                                        <TableCell component="th" scope="row">
                                            <Checkbox aria-label={ row.name } />
                                        </TableCell>
                                        <TableCell align="left">{ row.name }</TableCell>
                                        <TableCell align="left">{ row.link_with }</TableCell>
                                        <TableCell align="left">{ row.confidentiality }</TableCell>
                                        <TableCell align="left">
                                            <Stack direction="row" spacing={ 1 }>
                                                <IconButton  onClick={()=>{handleEditClick(row)}} aria-label="edit">
                                                    <EditIcon />
                                                </IconButton>
                                                <IconButton onClick={()=>{handleDeleteClick(row)}} aria-label="delete">
                                                    <DeleteIcon />
                                                </IconButton>
                                            </Stack>
                                        </TableCell>

                                    </TableRow>
                                )) }
                            </TableBody>
                        </Table>
                    </TableContainer>
                    {<Modal showModal={open} title={modalTitle} description={modalDescription} type={modalType} onClose={closeModel} />}

                </Box>
            </Box>
        </>
    );
};

export default Scenes;