import React from 'react'
import { Routes, Route } from 'react-router-dom';
import frontendUrl from '../../urls/frontendUrl';
import AppContainer from '../Container'
import Celebrity from '../../pages/Celebrity'
import Inlivers from '../../pages/Inlivers'
import Clubs from '../../pages/Clubs'
import Publication from '../../components/Publication'
import Profile from '../Card/Profile' 
import CardImage from '../Card/Image';
import Users from '../../pages/Inlivers/Users'
import Signalement from "../../pages/Signalement"
import Club from '../../pages/Clubs/Club'

export default function index() {
    return (
        <AppContainer>
            <Routes>
                <Route path={frontendUrl.HOME.SELF} element={<Dashboard />} />
                <Route path={frontendUrl.HOME.SELF} element={<Profile />} />
                <Route path={frontendUrl.HOME.MESSAGE} element={<Message />} />
                <Route path={frontendUrl.HOME.CELEBRITE} element={<Celebrity />} />
                <Route path={ frontendUrl.HOME.USERS } element={ <Inlivers /> } />
                <Route path={ frontendUrl.HOME.USER_SELECT } element={ <Users /> } />
                <Route path={ frontendUrl.HOME.CLUBS } element={ <Clubs /> } />
                <Route path={ frontendUrl.HOME.CLUB_SELECT } element={ <Club /> } />
                <Route path={frontendUrl.HOME.SIGNALEMENT} element={<Signalement />} />
            </Routes>
        </AppContainer>
    )
}

function Message() {
    return <div>
        <h2>Message</h2>
    </div>
}

function Dashboard() {
    return <div>
        <h2>Dashboard</h2>
    </div>
}