import * as React from 'react';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Box from '@mui/material/Box';
import TabItem from '../TabItem/index'

function a11yProps(index) {
  return {
    id: `simple-tab-${index}`,
    'aria-controls': `simple-tabpanel-${index}`,
  };
}

export default function BasicTabs({ tabElement }) {
  const [value, setValue] = React.useState(0);

  const handleChange = (event, newValue) => {
    setValue(newValue);
  };

  return (
    <Box sx={ { width: '100%', marginTop: -2 } }>
      <Box sx={ { borderBottom: 1, borderColor: 'divider' } }>
        <Tabs value={ value } onChange={ handleChange } aria-label="basic tabs example" variant="scrollable"
          scrollButtons="auto">
          {
            tabElement && tabElement.map((item, index) => <Tab sx={ { textTransform: 'none', marginLeft: -2 } } label={ item.tabTitle } id={index + 1} { ...a11yProps(index) } />)
          }
        </Tabs>
      </Box>
      {
        tabElement && tabElement.map((item, index) => (<TabItem key={ index } value={ value } index={ index }> { item.tabContent }</TabItem>))
      }
    </Box>
  );
}

