const Clubs = ({
    category,
    filterText = "Filtrer",
    filterHandle,
    secondHandleText = "Exporter",
    secondHandle, 
    windowHeight }) => {

    const navigate = useNavigate()
    return (
        <>
            <Stack direction="row" alignItems={ 'center' } justifyContent="space-between">
                <Grid sx={ { color: 'red' } }>
                    <Filter />
                </Grid>
                <Grid>
                    <CustomButton text={ secondHandleText } variant='contained' btnStyle={ { color: colors.dark.primary, backgroundColor: 'white' } } />
                </Grid>
            </Stack>

            <TableContainer component={ Paper } sx={ { marginTop: 2 } }>
                <Table sx={ { minWidth: 650 } } aria-label="caption table">
                    <TableHead style={ { backgroundColor: colors.light.gray200 } }>
                        <TableRow>
                            <TableCell><Checkbox aria-label='Select All' /></TableCell>
                            <TableCell>Nom<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Type<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Categorie<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Associé à<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Créateur<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Membres<i className='fa fa-sort-up'></i></TableCell>
                            <TableCell align="left">Badges<i className='fa fa-sort-up'></i></TableCell>
                        </TableRow>
                    </TableHead>
                    <TableBody>
                        { rows.map((row, index) => (
                            <TableRow key={ row.pseudo } sx={ { "&:hover": { bgcolor: colors.light.gray100, cursor: "pointer" } } }>
                                <TableCell component="th" scope="row">
                                    <Checkbox aria-label={ row.name } />
                                </TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">
                                    <Item imgSrc={ row.image_club } title={ row.name } titleStyle={ { fontWeight: '400' } } />
                                </TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">{ row.type }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">{ row.category }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">{ row.association }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">{ row.created_by }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">{ row.member }</TableCell>
                                <TableCell onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + row.id) } } align="left">{ row.badge }</TableCell>
                            </TableRow>
                        )) }
                    </TableBody>
                </Table>
            </TableContainer>
            <Box direction="row" alignItems={ 'center' } justifyContent="end" sx={ { display: "flex", paddingTop: 3, paddingBottom: 3 } }>
                <Pagination count={ rows.length } color="primary" shape="rounded" />
            </Box>
            
        </>
    );
};

export default Clubs;