import { Box, Card, CardContent, CardMedia, Grid, ImageList, ImageListItem, Typography } from '@mui/material';
import React from 'react';
import Image from '../../../../components/Card/Image'
import Info from '../../../../components/Card/Infos'
import colors from '../../../../constants/colors';
import Item from '../../../../components/Item'
import { useNavigate, useParams } from 'react-router-dom';
import frontendUrl from '../../../../urls/frontendUrl';

const clubs = [
    {
        id: 1,
        name: 'Club 1',
        type: 'Talent',
        category: 'Musique',
        association: 'Matt Pokora',
        created_by: 'Alex238',
        member: 234,
        badge: 'Diamand',
        reports: 4,
        image_club: 'https://media.istockphoto.com/id/583977698/vector/club-disco-flyer-template-with-music-elements.webp?s=2048x2048&w=is&k=20&c=PKjCiJAEW4huPOhLkXLxgU2RZPtsCMlx6cy-EqSVrXc='
    },
    {
        id: 2,
        name: 'Club 2',
        type: 'Talent',
        category: 'Danse',
        association: 'Beyonce',
        created_by: 'JohnDoe',
        member: 876,
        badge: 'Or',
        reports: 1,
        image_club: 'https://images.pexels.com/photos/1587927/pexels-photo-1587927.jpeg?auto=compress&cs=tinysrgb&w=1600'
    },
    {
        id: 3,
        name: 'Club 3',
        type: 'Talent',
        category: 'Artisanat',
        association: 'Les Compagnons du Devoir',
        created_by: 'JaneSmith',
        member: 542,
        badge: 'Argent',
        reports: 0,
        image_club: 'https://media.istockphoto.com/id/583977698/vector/club-disco-flyer-template-with-music-elements.webp?s=2048x2048&w=is&k=20&c=PKjCiJAEW4huPOhLkXLxgU2RZPtsCMlx6cy-EqSVrXc='
    },
    {
        id: 4,
        name: 'Club 4',
        type: 'Sport',
        category: 'Football',
        association: 'FC Barcelona',
        created_by: 'LionelMessi',
        member: 2098,
        badge: 'Diamand',
        reports: 10,
        image_club: 'https://images.pexels.com/photos/1587927/pexels-photo-1587927.jpeg?auto=compress&cs=tinysrgb&w=1600'
    },
];

const Friends = ({ rows, windowHeight }) => {
    const navigate = useNavigate()
    const params = useParams()
    const id = params.id - 1
    return (
        <>
            <Box >
                <Box sx={ { marginLeft: 3 } }>
                    <Typography sx={ { fontWeight: "bold", fontSize: 14, color: colors.light.gray90 } }>
                        { rows.length } amis
                    </Typography>
                </Box>
                <Grid container spacing={ 2 }  >
                    <Grid item xs={ 6 } md={ 8 } sx={ {
                        maxHeight: `${windowHeight - 150}px`, position: 'relative', width: '100%',
                        overflowY: 'scroll',
                        '&::-webkit-scrollbar': {
                            display: 'none',
                        },
                    } } >
                        <Box display="flex" flexDirection="row" flexWrap="wrap" gap={ 1 } justifyContent='space-between'>
                            { rows.map((user) => (
                                <Box onClick={ () => { navigate(frontendUrl.HOME.USERS + '/' + user.id) } } key={ user.id }>
                                    <Image pseudo={ user.pseudo } imgSrc={ user.image } type={ 'amis' } imgAmis={ 'https://i.pravatar.cc/150?img=5' } />
                                </Box>
                            )) }
                        </Box>
                    </Grid>
                    <Grid item xs={ 6 } md={ 4 } >
                        <Info
                            imgsrc={ rows[id].image }
                            name={ rows[id].pseudo }
                            depuis={ rows[id].createdAt }
                            right={ true }
                            style={ { bgcolor: 'white', borderRadius: '5px', width: '100%', boxShadow: '1' } }
                        />
                        <Box sx={ { marginTop: 3, borderRadius: '5px', boxShadow: '1', width: '100%', bgcolor: 'white' } } >
                            <Box sx={ { mt: 2, marginLeft: '20px', marginRight: "10px", p: 2, display: "flex", justifyContent: "space-between" } }>
                                <Typography sx={ { fontWeight: "bold", fontSize: 14, color: colors.dark.primary } }>Club commun ({ clubs.length })</Typography>
                            </Box>
                            { clubs.map((item) => (
                                <Box onClick={ () => { navigate(frontendUrl.HOME.CLUBS + '/' + item.id) } } sx={ { "&:hover": { bgcolor: colors.light.gray100, cursor: "pointer" } } } >
                                    <Box sx={ { marginLeft: '20px', marginRight: "10px", p: 1 } }>
                                        <Item imgStyle={ { borderRadius: "50%" } } imgSrc={ item.image_club } title={ item.name } />
                                    </Box>
                                </Box>
                            )) }
                        </Box>
                    </Grid>
                </Grid>
            </Box>
        </>
    );
};

export default Friends;