import { Grid } from '@mui/material'
import React from 'react'

export default function Media({ medias }) {
  return (
    <Grid>
      <img
        style={ { objectFit: 'cover', width: '100%', height: 400, backgroundColor: 'gray', borderRadius: 20 } }
        loading="lazy"
        alt=''
        src={ medias ? medias : '' }
      />
    </Grid>
  )
}
