const Reports = ({ windowHeight }) => {
    const [checked, setChecked] = useState([0]);
    const [hover, setHover] = useState({ state: false, index: '' })

    const handleOnMouseEnter = (index) => {
        setHover({ state: true, index: index })
    }

    const handleOnMouseLeave = (index) => {
        setHover({ state: false, index: '' })
    }


    const handleToggle = (value) => () => {
        const currentIndex = checked.indexOf(value);
        const newChecked = [...checked];

        if (currentIndex === -1) {
            newChecked.push(value);
        } else {
            newChecked.splice(currentIndex, 1);
        }

        setChecked(newChecked);
    };

    return (
        <>
            <Box >
                <Grid container spacing={ 2 } columns={ 16 }>
                    <Grid item xs={ 8 } >
                        <List
                            sx={ {
                                bgcolor: 'white', p: 0, overflow: 'auto', maxHeight: `${windowHeight - 170}px`, position: 'relative',
                                overflowY: 'scroll',
                                '&::-webkit-scrollbar': {
                                    display: 'none',
                                },
                            } }
                        >
                            { list.map((item) => {
                                return (
                                    <ListItem
                                        onMouseEnter={ () => handleOnMouseEnter(item.id) }
                                        onMouseLeave={ () => handleOnMouseLeave(item.id) }
                                        key={ item.id }
                                        secondaryAction={ hover?.index === item.id ?
                                            <IconButton edge="end" aria-label="comments">
                                                <DeleteIcon />
                                            </IconButton>
                                            :
                                            null
                                        }
                                        disablePadding
                                    >
                                        <ListItemButton role={ undefined } onClick={ handleToggle(item.id) } dense>

                                            <ListItemText alignItems="flex-start" sx={ { p: 0, m: 0, height: "80px", display: "flex", alignItems: "center" } } >
                                                <ItemList
                                                    message={ false }
                                                    imgSrc={ item.imgSrc }
                                                    subTitle={ item.subTitle }
                                                    title={ item.title }
                                                />
                                            </ListItemText>
                                        </ListItemButton>
                                        { list.length !== item.id && <Divider component="li" /> }
                                    </ListItem>
                                )
                            }) }
                        </List>
                    </Grid>
                    <Grid item xs={ 8 } >
                        <Item>
                            <Box>
                                <Box sx={ { p: 2, } }>
                                    <Typography sx={ { fontSize: 16, color: colors.cost.bluegray } } variant="body2" >
                                        Leilaaaand (leila.poulie@gmail.com)
                                    </Typography>
                                    <Typography sx={ { fontSize: 14, fontWeight: '600', color: colors.cost.blue } } gutterBottom variant="h5" component="p">
                                        Incitation/discour haineux, propos racistes
                                    </Typography>
                                </Box>
                                <Divider sx={ { height: 1, bgcolor: 'black' } } />
                                <Box sx={ {
                                    p: 0, overflow: 'auto', maxHeight: `${windowHeight - 250}px`, position: 'relative',
                                    overflowY: 'scroll',
                                    '&::-webkit-scrollbar': {
                                        display: 'none',
                                    },
                                } }>
                                    {
                                        chats.map((item) => 
                                            (<ChatItem
                                                pseudo={ item.pseudo }
                                                avatar={ item.avatar }
                                                content={ item.content }
                                                date={ item.date }
                                            />)
                                        )
                                    }
                                </Box>
                            </Box>
                        </Item>
                    </Grid>
                </Grid>
            </Box>
        </>
    );
};

export default Reports;